.pdct ul {
    text-align: left;
    color:#06001E;
}

.pdct ul li:before {
  content: " ";
  display: block;
  border: solid 0.8em #06001E;
  border-radius: .8em;
  height: 0;
  width: 0;
  position: absolute;
  left: 0em;
  top: 40%;
  margin-top: -0.5em;
}


.pdct ul li:after {
  content: " ";
  display: block;
  width: 0.5em;
  height: 0.8em;
  border: solid #D6B9EF;
  border-width: 0 0.2em 0.2em 0;
  position: absolute;
  left: 0.5em;
  top: 40%;
  margin-top: -0.2em;
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}

.pdct ul li {
  font-size: 16px;
  list-style-type: none;
  margin-bottom: 1em;
  padding: 0.25em 0 0 2.5em;
  position: relative;
}

