.banner-overlay {
	position:absolute;
	top:0;
	left:0;
	right:0;
	bottom:0;
	display:flex;
	color:white;
	align-items:center;
	justify-content:flex-end;
	background: linear-gradient(to left top, rgba(6, 0, 30,0.95) 24%, rgba(6, 0, 30, 0.85) 28%, rgba(6, 0, 30, 0.8) 35%, rgba(6, 0, 30, 0.65) 40%, rgba(6, 0, 30, 0.3) 44%, rgba(6, 0, 30, 0.25) 45%, rgba(6, 0, 30, 0.2) 46%, rgba(6, 0, 30, 0.15) 47%, rgba(6, 0, 30, 0.1) 48%, rgba(6, 0, 30, 0) 49%, transparent 56%);
	
	.banner-container{
		position: relative;
    top: 15%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
		align-items: flex-end;
		div{
			width: 30%;
			h3{
				color: #e1dff2;
			}
		}
	}
}

